export default {
	data() {
		return {
			phone:'',
			isshow:false
		};
	},
	methods:{
		logout(){
			this.isshow=true
		},
		cancel(){
			this.isshow=false
		},
		sureout(){
			uni.removeStorage({
				key:'user_phone',
				success(res) {
					uni.redirectTo({
						url:'../../pages/login/login'
					})
						
					
				}
			})
		}
	},
	created() {
		/* 获取用户手机号 */
		let self=this
		uni.getStorage({
			key:'user_phone',
			success(res) {
				self.phone=res.data
			}
		})
	}
}
